.card {
  display: inline-block;
  margin-bottom: 10px;
  padding: 1rem 1rem;
  width: 300px;
  /* max-width: 300px; */
  border-radius: 10px;

  background-color: rgba(255, 255, 255, 0.15);
}
.card2 {
  display: inline-block;
  margin-bottom: 10px;
  padding: 1rem 1rem;
  /* width: 300px; */
  max-width: 300px;
  border-radius: 10px;

  background-color: rgba(255, 255, 255, 0.15);
}

.cardImage {
  margin-bottom: 0.5rem;
}

.image {
  object-fit: cover;
}
.image img {
  max-width: calc(300px - 2rem);
}

.cardContent {
  margin: 0;
}

.content {
  display: flex;
  flex-direction: column;
}
.content h3 {
  margin-bottom: 0.25rem;
}
.content span {
  margin-top: 0.75rem;
}
